import Vue from 'vue'
import Router from 'vue-router'
import App from './App.vue'
// import Home from './views/home/Home.vue'
// import City from './views/city/City.vue'
// import Msite from './views/msite/Msite.vue'
Vue.use(Router)

// const Home = () =>
//     import ('./views/home/Home.vue')
const Home = r => require.ensure([], () => r(require('./views/home/Home.vue')), 'Home')
const City = r => require.ensure([], () => r(require('./views/city/City.vue')), 'City')
const Msite = r => require.ensure([], () => r(require('./views/msite/Msite.vue')), 'Msite')
const Login = r => require.ensure([], () => r(require('./views/login/Login.vue')), 'Login')
const Seek = r => require.ensure([], () => r(require('./views/seek/Seek.vue')), 'Seek')
const Mine = r => require.ensure([], () => r(require('./views/mine/Mine.vue')), 'Mine')
const Indent = r => require.ensure([], () => r(require('./views/indent/Indent.vue')), 'Indent')
const Info = r => require.ensure([], () => r(require('./views/info/Info.vue')), 'Info')
const Address = r => require.ensure([], () => r(require('./views/info/address/Address.vue')), 'Address')
const Add = r => require.ensure([], () => r(require('./views/info/address/add/Add.vue')), 'Add')
const AddDetail = r => require.ensure([], () => r(require('./views/info/address/add/addDetail/AddDetail.vue')), 'AddDetail')
const Shop = r => require.ensure([], () => r(require('./views/msite/shop/Shop.vue')), 'Shop')
const Order = r => require.ensure([], () => r(require('./views/msite/shop/order/Order.vue')), 'Order')

// import Address from '@/views/info/address/Address.vue'
export default new Router({
    routes: [{
        path: '/',
        component: App, //顶级路由
        children: [
            //地址为空跳转home页面
            {
                path: '',
                redirect: '/home',
            },
            //首页城市页面
            {
                path: '/home',
                name: 'Home',
                component: Home
            },
            //地址搜索页面
            {
                path: '/city/:id',
                name: 'City',
                component: City
            },
            //地址商品页面
            {
                path: '/msite',
                name: 'Msite',
                component: Msite
            },
            //登录页
            {
                path: '/login',
                name: 'Login',
                component: Login
            },
            //搜素页
            {
                path: '/seek',
                name: 'Seek',
                component: Seek
            },
            //我的页
            {
                path: '/mine',
                name: 'Mine',
                component: Mine,
            },
            //订单页
            {
                path: '/indent',
                name: 'Indent',
                component: Indent
            },
            //订单页
            {
                path: '/info',
                name: 'Info',
                component: Info
            },
            //获取地址
            {
                path:"/address",
                name:Address,
                component:Address
            },
             //新地址
             {
                path:"/add",
                name:Add,
                component:Add,
                // meta:{keepAlive:true}

                
            },
            //地址
            {
                path:"/addDetail",
                name:AddDetail,
                component:AddDetail,
                
            },
            //商品详情
            {
                path:"/shop",
                name:Shop,
                component:Shop
            },
             //确认订单
             {
                path:"/order",
                name:Order,
                component:Order
            }

            
        ]
    }]
})