import Vue from "vue"
import Vuex from "vuex"
import createPersistedState from "vuex-persistedstate"
Vue.use(Vuex);
let store=new Vuex.Store({
    // 自动存储
    plugins: [createPersistedState()],
    // 状态
    state:{
        ma:"",
        dizhi:"",
        avaimg:"",
        gwc:[],
    
    },
    // 修改
    getters:{
        sum:(state)=>{
            let a=0;
            for(let i in state.gwc){
                a+=state.gwc[i].num;
            }
            return a;
        },
        jiage:(state)=>{
            
            let a=0;
            for(let i in state.gwc){
                a+=state.gwc[i].num*state.gwc[i].price;
            }
            return a;
        },
    },
    // 计算属性
    mutations:{
        fang(state,v){
            state.ma=v
        },
        tui(state){
            state.ma=""
            
        },
        dizhi(state,v){
            state.dizhi=v
        },
        avatar(state,v){
            state.avaimg=v
        },
        add(state,val){
            console.log(val)
            // state.gwc.push(val);
            let arr = [...state.gwc];
            let yy=arr.some((v)=>{
                return v.item_id==val.item_id
            })
            if(yy==false){
                val.num=1;
                arr.push(val);
            }
            if(yy==true){
                for(let i=0;i<arr.length;i++){
                    if(arr[i].item_id==val.item_id){
                        arr[i].num+=1;
                    }
                }
            }
            return state.gwc=arr;
        },
        jian(state,id){
            console.log(state); 
            let arr = [...state.gwc];
            for(let i=0;i<arr.length;i++){
                if(arr[i].item_id==id){
                    if(arr[i].num>1){
                         arr[i].num--;
                    }
                   else{
                       if(arr[i].num==1){
                            if(confirm("确定删除吗")){
                                arr.splice(i,1)
                            }
                       }
                   }
                }
            }
            return state.gwc=arr;
        },
        jia(state,id){
            console.log(state); 
            let arr = [...state.gwc];
            for(let i=0;i<arr.length;i++){
                if(arr[i].item_id==id){   
                         arr[i].num+=1;
                }
            }
            return state.gwc=arr;
        },
        qkgwc(state){
            state.gwc=[]
        }
        
    },
    actions:{
       
    }
    
})
export default store;